<template>
    <div class="aboutUs" style="overflow: auto;">
        <p v-html="infor"></p>
    </div>
</template>
<script>
    export default{
        name:'about',
        data(){
            return {
                infor:''
            }
        },
        //    keep-alive
        //   header footer   watch 
        //keep-alive mounted
        mounted(){
            this.api.get(this.baseUrl+'/api/aboutus/sellers').then((d)=>{
                this.infor=d.data.data.content;
            })
        }
    }
</script>
<style>

</style>



