import Vue from 'vue'
import App from './App'
import router from './router'
import store from "./store";
Vue.use(store);
import "./assets/css/all.css"; //css
require('./assets/js/jquery-1.11.0'); //jquery

import axios from "axios";
import Mint from 'mint-ui'; //mint ui 
import 'mint-ui/lib/style.css'; // mint ui css
Vue.use(Mint);

//vue-awesome-swiper----------start mintui swiper  
require('swiper/dist/css/swiper.css');
import VueAwesomeSwiper from 'vue-awesome-swiper'
Vue.use(VueAwesomeSwiper)
//vue-awesome-swiper------------end

Vue.prototype.$http=axios; // 
Vue.prototype.baseUrl="http://127.0.0.1:3301";

//
import * as custom from './filter';
//
Object.keys(custom).forEach(key => {
    //console.log(key) //
    //console.log(custom[key])
    //Vue.filter(key, custom[key])
});
for(var x in custom){
    Vue.filter(x,custom[x])
}

//
import * as direct from './directive';
Object.keys(direct).forEach(key => {
    console.log(key)
    console.log(direct[key])
    Vue.directive(key,direct[key])
});

//
import * as utils from "./utils";
Vue.use(utils);

//
Vue.prototype.busEvent=new Vue();

//Aai
import Api from "./api";
Vue.prototype.api=Api;

axios.interceptors.request.use(function(config) {
    console.log('');
    if (localStorage.getItem("user")) {
        config.headers['token'] = JSON.parse(localStorage.getItem("user")).token;
    }
    return config
}, function(error) {
    console.log('');
    return error
});

//()
axios.interceptors.response.use(res => {
    console.log('');
    if(res.status==200){
        // 
        console.log(res);
        if(res.data.code==400){
            //router.push('/login')
            console.log('')
        }
    }else{

    }
    return res;
}, (error) => {
    console.log('');
    return error;
});


var token=null;
if(window.localStorage.getItem("userInfoObj")){
    token=JSON.parse(window.localStorage.getItem("userInfoObj")).token
}
Vue.prototype.token=token;

var glob=new Vue({
    el: '#app',
    router,
    store,
    template: '<App/>',
    components: {App}
});
Vue.prototype.glob=glob;



