<template>
    <div class="fapiaoHaHa" style="overflow:auto;">

        <div class="fpBg">
            <div class="fpCenter">
                <p class="p1 keKaiPrice">{{keCaiPrice}}</p>
                <p class="p2"></p>
            </div>
        </div>

        <div class="lishiKai">
            <p class="kaiPiaoTitel"></p>
            <p class="kaiPiaoJine">:<b id="zongKaipiao">{{zongKai}}</b></p>
        </div>

        <ul class="goods goodsKaipiao">
            <li class="goGoodes goGoodes2" v-for="(d,index) in historyList">
				<p class="p1">
					<span></span>
					<span>{{d.created_at}}</span>
				</p>
				<p class="p1">
					<span></span>
					<span>{{d.title.title}}</span>
				</p>
				<p class="p1">
					<span></span>
					<span>{{d.title.item.item}}</span>
				</p>
				<p class="p1">
					<span></span>
					<span class="thisPrice">{{d.amount}}</span>
				</p>
				<a class="kaipiaoZhuangTai">{{d.is_mall?'':''}}</a>
			</li>
        </ul>

        <a class="recivBtn bottom weiColor" v-if="minKeCai>keCaiPrice">
            :{{minKeCai}}
        </a>
        <router-link :to="{
            path:'/myApplyFapiao',
            query:{
                keCaiPrice:keCaiPrice,
                minKeCai:minKeCai
            }
        }" class="recivBtn bottom" v-else>
            
        </router-link>

    </div>
</template>

<script>
    let obj = null;
    export default{
        name: 'fapiao',
        data(){
            return {
                keCaiPrice:'',
                minKeCai:'',
                zongKai:'',
                historyList:[],
                token: window.localStorage.getItem('userInfoObj') ? JSON.parse(window.localStorage.getItem('userInfoObj')).token : ''
            }
        },
        methods: {
            //
            canKai(){
                this.api.get(this.baseUrl + '/api/invoice/info?token=' + this.token).then(d => {
                    if(d.data.data.can_invioce>0){
                        // 0
                        this.keCaiPrice=d.data.data.can_invioce;
                        // 100
                        this.minKeCai=d.data.data.min_money;
                        //
                        this.zongKai=d.data.data.invoice_money;
                    }else{
                        //
                        // 0
                        this.keCaiPrice=1000;
                        // 100
                        this.minKeCai=100;
                        //
                        this.zongKai=450;
                    }
                })
            },
            history(){
                this.api.get(this.baseUrl + '/api/invoice/list?token=' + this.token).then(d => {
                    var data=d.data.data.data;
                    if(data.length>0){
                        this.historyList=data;
                    }else{
                        data=[
                            {
                                is_mall:0,//0   1 
                                created_at:4564564512,
                                title:{
                                    title:'',//
                                    item:{
                                        item:''//
                                    }
                                },
                                amount:100//
                            },
                            {
                                is_mall:1,//0   1 
                                created_at:4564564512,
                                title:{
                                    title:'1',//
                                    item:{
                                        item:'2'//
                                    }
                                },
                                amount:50//
                            },
                            {
                                is_mall:0,//0   1 
                                created_at:4564564512,
                                title:{
                                    title:'',//
                                    item:{
                                        item:''//
                                    }
                                },
                                amount:100//
                            },
                            {
                                is_mall:1,//0   1 
                                created_at:4564564512,
                                title:{
                                    title:'1',//
                                    item:{
                                        item:'2'//
                                    }
                                },
                                amount:50//
                            },
                            {
                                is_mall:0,//0   1 
                                created_at:4564564512,
                                title:{
                                    title:'',//
                                    item:{
                                        item:''//
                                    }
                                },
                                amount:100//
                            },
                            {
                                is_mall:1,//0   1 
                                created_at:4564564512,
                                title:{
                                    title:'1',//
                                    item:{
                                        item:'2'//
                                    }
                                },
                                amount:50//
                            }
                        ];
                        this.historyList=data;
                    }
                })
            }
        },
        mounted(){
            this.$store.dispatch('changeFooter');
            this.canKai();
            this.history();
        }
    }
</script>
<style>

</style>



